import React, { useState } from "react";
import { createStyles, Theme, makeStyles } from "@material-ui/core/styles";
import { TextField, Button, Collapse } from "@material-ui/core";
import { CloudUpload, RateReview } from "@material-ui/icons";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Page from "./Page";
import checkValidation from "../api/check-validation";
import { Alert, AlertTitle } from "@material-ui/lab";
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      "& > *": {
        margin: theme.spacing(1),
      },
    },
    marginTop: {
      marginTop: "2%",
    },
    paper: {
      marginTop: "0.5%",
    },
  })
);
function constructQueryParamerts(gitHubUrls: string[]) {
  let queryParamerts: any = {};

  gitHubUrls.forEach((url, index) => {
    queryParamerts[`GitHubUrl_${index + 1}`] = url;
  });
  return queryParamerts;
}
export default function Inputs() {
  const classes = useStyles();

  function setValidationResponse(res: any) {
    Object.keys(res).forEach((key) => {
      eval(`set${key}_validationResponse('${res[key].split("\n").join()}');`);
    });
  }

  const [gitHubUrl_1, setGitHubUrl_1] = useState("");
  const [gitHubUrl_2, setGitHubUrl_2] = useState("");
  const [gitHubUrl_3, setGitHubUrl_3] = useState("");
  const [gitHubUrl_4, setGitHubUrl_4] = useState("");

  const [
    gitHubUrl_1_validationResponse,
    setGitHubUrl_1_validationResponse,
  ] = useState("1");
  const [
    gitHubUrl_2_validationResponse,
    setGitHubUrl_2_validationResponse,
  ] = useState("2");
  const [
    gitHubUrl_3_validationResponse,
    setGitHubUrl_3_validationResponse,
  ] = useState("3");
  const [
    gitHubUrl_4_validationResponse,
    setGitHubUrl_4_validationResponse,
  ] = useState("4");

  return (
    <Page>
      <form autoComplete="off">
        <Typography variant="h3">
          Welcome to the Development Engineering
        </Typography>
        <Typography variant="h5">
          CI/CD Self-Help Configuration File(s) Validation Portal
        </Typography>

        <div className={classes.marginTop}>
          <TextField
            value={gitHubUrl_1}
            onChange={(event) => setGitHubUrl_1(event.target.value)}
            error={false}
            type="url"
            id="outlined-success-helper-text"
            label="GitHub URL"
            placeholder="https://github.com/raykauray/test-repos/blob/master/app.yaml"
            helperText=""
            variant="outlined"
          />
          <Collapse in={Boolean(gitHubUrl_1_validationResponse.length)}>
            <Alert
              severity="success"
              onClose={() => {
                setGitHubUrl_1_validationResponse("");
              }}
            >
              <AlertTitle>Error</AlertTitle>
              {gitHubUrl_1_validationResponse}
            </Alert>
          </Collapse>
        </div>

        <div className={classes.marginTop}>
          <TextField
            value={gitHubUrl_2}
            onChange={(event) => setGitHubUrl_2(event.target.value)}
            error={false}
            id="outlined-error-helper-text"
            label="GitHub URL"
            placeholder="http://github.com/yaml"
            helperText=""
            variant="outlined"
          />
          <Collapse in={Boolean(gitHubUrl_2_validationResponse.length)}>
            <Alert
              severity="error"
              onClose={() => {
                setGitHubUrl_2_validationResponse("");
              }}
            >
              <AlertTitle>Error</AlertTitle>
              This is an error alert â€” <strong>check it out!</strong>
            </Alert>
          </Collapse>
        </div>
        <div className={classes.marginTop}>
          <TextField
            value={gitHubUrl_3}
            onChange={(event) => setGitHubUrl_3(event.target.value)}
            error={false}
            id="outlined-error-helper-text"
            label="GitHub URL"
            placeholder="http://github.com/yaml"
            helperText=""
            variant="outlined"
          />
          <Collapse in={Boolean(gitHubUrl_3_validationResponse.length)}>
            <Alert
              severity="error"
              onClose={() => {
                setGitHubUrl_3_validationResponse("");
              }}
            >
              <AlertTitle>Error</AlertTitle>
              {gitHubUrl_3_validationResponse}
            </Alert>
          </Collapse>
        </div>
        <div className={classes.marginTop}>
          <TextField
            value={gitHubUrl_4}
            onChange={(event) => setGitHubUrl_4(event.target.value)}
            error={false}
            id="outlined-error-helper-text"
            label="GitHub URL"
            placeholder="http://github.com/yaml"
            helperText=""
            variant="outlined"
          />
          <Collapse in={Boolean(gitHubUrl_4_validationResponse.length)}>
            <Alert
              severity="error"
              onClose={() => {
                setGitHubUrl_4_validationResponse("");
              }}
            >
              <AlertTitle>Error</AlertTitle>
              {gitHubUrl_4_validationResponse}
            </Alert>
          </Collapse>
        </div>
        <div>
          <Button
            className={classes.marginTop}
            variant="contained"
            color="default"
            startIcon={<CloudUpload />}
            style={{ marginRight: "2%" }}
          >
            Upload Files
          </Button>
          <Button
            className={classes.marginTop}
            variant="contained"
            color="primary"
            startIcon={<RateReview />}
            onClick={async () => {
              let queryParamerts = constructQueryParamerts([
                gitHubUrl_1,
                gitHubUrl_2,
                gitHubUrl_3,
                gitHubUrl_4,
              ]);
              let res = await checkValidation(queryParamerts);
              setValidationResponse(res);
            }}
          >
            Review Files
          </Button>
        </div>
      </form>
    </Page>
  );
}
